@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
*{
  margin:0px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.css-i4bv87-MuiSvgIcon-root {
  margin: 0px 10px;
}
.App{
  display:grid;
  grid-template-columns: 300px auto 50px;
}
.loginwrap img{
  width:100px;
}
.loginwrap{
  background: rgb(249, 250, 248);
  padding: 20px;
  width:500px;
  margin: 0px auto;
  display:flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
 
}
.loginwrap button{
  width:100%;
  background: #2381fa;
  padding: 10px 20px;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 16px;
  border-radius: 10px;
  margin-top: 20px;
  font-size: 20px;
}
.head{
  margin-top: 5px;
  font-family: 'Lobster', cursive;
  font-size: 26px;
  font-weight: 500;
}

.header{
    display:grid;
    grid-template-columns: 300px auto 200px;
    align-items: flex;
    height: 80px;
    width: 100%;
    padding-top: 20px;
    padding-left: 1%;
    padding-right: 25px;    
    border-bottom: 1px solid light gray;
}
.header__logo{
    display: flex;
    padding-left: 6%;
    vertical-align: middle;
}
.header__logo img{
    width:60px;
    padding-top: 8px;
}
.header__logo span{
    font-size: 18px;
    color:black;
    padding-left: 15px;
    vertical-align: middle;
}
.header__search{
    display:flex;
    align-items: center;
    width: 55%;
    height: 50px;
    background-color: whitesmoke;
    padding:12px;
    border-radius: 10px;
    margin-left: 20%;
    margin-right: 10%;
    min-width: 500px;
}
.header__search button{
    background-color: transparent;
    border: transparent;
}
.AlignIcon{
    margin-left: 10%;
    position: inherit;
}
.header__search input{
    background-color: transparent;
    border:0;
    outline:0;
    flex:1 1;
}
.header__icons{
    display:flex;
    align-self: start;
    align-items: center;
    padding-left:950px;
}

.header__icons span{
    display:flex;
    align-items: center;
    margin-left: 10px;
}
.header__icons span:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    cursor: pointer;
}

#SettingsIcon{
    display:flex;
    align-items: center;
    margin-left: 10px;
}

#SettingsIcon:hover{
    -webkit-transform: rotate(45deg) scale(1.2);
            transform: rotate(45deg) scale(1.2);
    transition-duration: 0.5s;
    cursor: pointer;
}

#Avatar{
    display:flex;
    align-items: center;
    margin-left: 25px;
    margin-right: 6%;
}

#Avatar:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition-duration: 0.5s;
}

.floatRight{
    float:right;
}
.sidebar{
    /* background-color: rgb(250, 250, 250); */
    /* background-color: chartreuse; */
    height: auto;
    align-content: center;
}
.sidebar_btn img{
    width:24px;
    height:24px;
}
.sidebar_btn button{
    background-color: transparent;
    border: 2px solid rgb(74, 120, 247);
    display: flex;
    width: 200px;
    height: 50px;
    align-items: center;
    border-radius: 40px;
    padding-left: 25px;
    margin-left: 25px;
    margin-bottom: 25px;
    margin-top: 15px;
}
.sidebar_btn button span{
    font-size: 16px;
    margin-right: 20px;
    margin-left: 15px;
    font-weight: 400;
}
.sidebar_btn button:hover{
    font-size: 18px;
    margin-right: 20px;
    margin-left: 25px;
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    transition: all;
}
.sidebar_btn img:hover {
    cursor: pointer;
    width:30px;
}
.sidebar__option{
    margin-top: 10px;
}
.sidebar__option{
    display:flex;
    align-items: center;
    margin:15px;
    margin-left: 15px;
    padding:8px 20px;
}
.sidebar__option:hover{
    background-color: rgba(241, 146, 146, 0.807);
    cursor: pointer;
    border-left: 6px solid red;
    font-weight: 900;
}
.sidebar__option span{
    margin-left: 15px;
    font-size: 13px;
    font-weight: 500;
    color:rgba(78, 78, 78, 0.581);
}

#background { 
    width: 100vw;
    min-height: 100vh;
}

